<template>
  <div class="posts">
    <div class="main-posts"> 
    <p>Faça a sua votação</p>
    <input type="radio" id="one" value="91" v-model="picked">
    <label for="one">91 - Futebol (Partido dos Esportes)</label>
    <br>
    <input type="radio" id="two" value="92" v-model="picked">
    <label for="two">92 - Rock (Partido dos Ritmos Musicais)</label>
    <br>
    <input type="radio" id="two" value="93" v-model="picked">
    <label for="two">93 - Professora (Partido das Profissoes)</label>
    <br>
    <input type="radio" id="two" value="94" v-model="picked">
    <label for="two">94 - Copa do Mundo (Partido das Festas Populares)</label>
    <br>
    <input type="radio" id="two" value="95" v-model="picked">
    <label for="two">95 - Vitória Régia (Partido do Folclore)</label>
    <br>
    <br>
    <span v-if="picked">
      Selecionado:
      <b>{{ picked }}</b>
    </span>

    <!--span><b>{{ response }}</b></span><br /-->
    <form v-on:submit="castBallot">
      <!-- <input type="text" value="2sww593dc034wb2twdk91r" v-model="input.electionId"  >
      <br>-->
      <input type="text" v-model="input.voterId" placeholder="Insira sua identificação">
      <br>
      <br>
      <input type="submit" value="Votar">
    </form>

    <br>
    <span v-if="response">
      <b>{{ response }}</b>
    </span>
    <br>
    <vue-instant-loading-spinner id="loader" ref="Spinner"></vue-instant-loading-spinner>
    </div>
  </div>
</template>

<script>
import PostsService from "@/services/apiService";
import VueInstantLoadingSpinner from "vue-instant-loading-spinner/src/components/VueInstantLoadingSpinner.vue";

export default {
  name: "response",
  data() {
    return {
      input: {},
      picked: null,
      response: null
    };
  },
  components: {
    VueInstantLoadingSpinner
  },
  methods: {
    async castBallot() {
      await this.runSpinner();

      const electionRes = await PostsService.queryWithQueryString('election');

      let electionId = electionRes.data[0].Key;

      console.log("picked: ");
      console.log(this.picked);
      console.log("voterId: ");
      console.log(this.input.voterId);
      this.response = null;

 

      //error checking for making sure to vote for a valid party
      if (this.picked === null ) {
        console.log('this.picked === null')

        let response = "Por favor, escolha um partido";
        this.response = response;
        await this.hideSpinner();
      
      } else if (this.input.voterId === undefined) {
        console.log('this.voterId === undefined')

        let response = "É necessário inserir sua identificação";
        this.response = response;
        await this.hideSpinner();

      } else {

        const apiResponse = await PostsService.castBallot(
          electionId,
          this.input.voterId,
          this.picked
        );

        console.log('apiResponse: &&&&&&&&&&&&&&&&&&&&&&&');
        console.log(apiResponse);

        if (apiResponse.data.error) {
          this.response= apiResponse.data.error;
          await this.hideSpinner();
        } else if (apiResponse.data.message) {
          this.response= `Não foi possível encontrar eleitor com identificação ${this.input.voterId}.
          Certifique-se de inserir um número de eleitor válido`;
          await this.hideSpinner();
        } 
        else {
          let response = `Voto registrado com sucesso para o partido ${this.picked} para eleitor  
            com identificação ${apiResponse.data.voterId}. Obrigada por fazer sua parte e
            votar!`;

          this.response = response;

          console.log("cast ballot");
          console.log(this.input);
          await this.hideSpinner();
        }
      }
    },
    async runSpinner() {
      this.$refs.Spinner.show();
    },
    async hideSpinner() {
      this.$refs.Spinner.hide();
    }
  }
};
</script>
