<template>
  <div class="posts">
    <div class="main-login">
      <p>Eleições 2020</p>
      <img
        width="54"
        height="54"
        src="http://www.tse.jus.br/temas/imagens/temas-categorias/eleicoes.png"
        alt="Eleições"
      />
      <p>
        Se você é um eleitor registrado, insira a sua identificação de
        eleitor ou solicite o seu cadastro.
      </p>

      <!-- <p> -->
      <!--span><b>{{ response }}</b></span><br /-->
      <!-- </p> -->

      <form v-on:submit="validateVoter">
        <input
          type="text"
          v-model="loginData.voterId"
          placeholder="Insira sua identificação"
        />
        <br />

        <span v-if="loginReponse">
          <b>{{ loginReponse.data }}</b>
        </span>
        <br />
        <input type="submit" value="Entrar" />
      </form>
    </div>

    <br />
    <div class="main-register">
      <p>Cadastramento de eleitores</p>
      <form v-on:submit="registerVoter">
        <input
          type="text"
          v-model="registerData.voterId"
          placeholder="Insira a identificação"
        />
        <br />
        <input
          type="text"
          v-model="registerData.registrarId"
          placeholder="Insira a seção"
        />
        <br />
        <input
          type="text"
          v-model="registerData.firstName"
          placeholder="Insira o primeiro nome"
        />
        <br />
        <input
          type="text"
          v-model="registerData.lastName"
          placeholder="Insira o sobrenome"
        />
        <br />
        <span v-if="registerReponse">
          <b>{{ registerReponse.data }}</b>
        </span>
        <br />
        <input type="submit" value="Registrar" />
      </form>
    </div>

    <vue-instant-loading-spinner
      id="loader"
      ref="Spinner"
    ></vue-instant-loading-spinner>
  </div>
</template>

<script>
import PostsService from "@/services/apiService";
import VueInstantLoadingSpinner from "vue-instant-loading-spinner/src/components/VueInstantLoadingSpinner.vue";

export default {
  name: "response",
  data() {
    return {
      loginData: {},
      registerData: {},
      registerReponse: {
        data: "",
      },
      loginReponse: {
        data: "",
      },
    };
  },
  components: {
    VueInstantLoadingSpinner,
  },
  methods: {
    async registerVoter() {
      await this.runSpinner();

      if (
        !this.registerData.voterId ||
        !this.registerData.registrarId ||
        !this.registerData.firstName ||
        !this.registerData.lastName
      ) {
        console.log("!thislogin");
        let response = "Insira todos os campos";
        this.registerReponse.data = response;
        await this.hideSpinner();
      } else {
        const apiResponse = await PostsService.registerVoter(
          this.registerData.voterId,
          this.registerData.registrarId,
          this.registerData.firstName,
          this.registerData.lastName
        );
        console.log(apiResponse);
        this.registerReponse = apiResponse;
      }

      await this.hideSpinner();
    },

    async validateVoter() {
      await this.runSpinner();

      if (!this.loginData.voterId) {
        console.log("!thislogin");
        let response = "Por favor, insira o ID";
        this.loginReponse.data = response;
        await this.hideSpinner();
      } else {
        const apiResponse = await PostsService.validateVoter(
          this.loginData.voterId
        );
        console.log("apiResponse");
        console.log(apiResponse.data);

        if (apiResponse.data.error) {
          // console.log(apiResponse);
          console.log(apiResponse.data.error);
          this.loginReponse = apiResponse.data.error;
        } else {
          this.$router.push("castBallot");
        }

        console.log(apiResponse);
        this.loginReponse = apiResponse;
        // this.$router.push('castBallot')
        await this.hideSpinner();
      }
    },
    async runSpinner() {
      this.$refs.Spinner.show();
    },
    async hideSpinner() {
      this.$refs.Spinner.hide();
    },
  },
};
</script>

